/* --------------------------------------------
    Typography 
 ---------------------------------------------- */


$heading-font: 'Montserrat', sans-serif;
$body-font: 'Pathway Extreme', sans-serif;


body {
    font-family: $body-font;
    font-size: 17px;
    font-weight: 400;
    line-height: 32px;
    color: #696969;
    background-color: #fff;
}


h1,
h2,
h3,
h4,
h5,
h6 {

    font-family: $heading-font;
    margin: 0px;
    font-weight: 700;
    line-height: 120%;
}

h1 {
    font-size: 2.986rem;
}

h2 {
    font-size: 2.488rem;
}

h3 {
    font-size: 2.074rem;
}

h4 {
    font-size: 1.728rem;
}

h5 {
    font-size: 1.44rem;
}

h6 {
    font-size: 1rem;
}

a {
    text-decoration: none;
    font-weight: 600;
    outline: none !important;
    cursor: pointer;

    @include transition(.4s);
    color: #211e3b;
}

p {
    margin: 0px;
}